"use client";

import { trpc } from "@/lib/trpc";
import { DataTable } from "@repo/ui/components/data-table";
import { ColumnDef } from "@tanstack/react-table";
import { Button } from "@repo/ui/components/button"; // Pro test refetche

// Definice typu dat (shoduje se s tím, co posílá API)
type User = {
  id: string;
  name: string;
  email: string;
  role: "Admin" | "User" | "Guest";
  status: "Active" | "Pending" | "Banned";
};

// Definice sloupců pro TanStack Table
const columns: ColumnDef<User>[] = [
  {
    accessorKey: "id",
    header: "ID",
    cell: ({ row }) => <span className="text-muted-foreground text-xs">{row.getValue("id")}</span>
  },
  {
    accessorKey: "name",
    header: "Jméno",
    cell: ({ row }) => <span className="font-medium">{row.getValue("name")}</span>
  },
  {
    accessorKey: "email",
    header: "Email",
  },
  {
    accessorKey: "role",
    header: "Role",
    cell: ({ row }) => {
      const role = row.getValue("role") as string;
      // Podmíněné stylování podle role
      return (
        <span className={role === "Admin" ? "font-bold text-red-600 bg-red-100 px-2 py-1 rounded text-xs" : "bg-gray-100 px-2 py-1 rounded text-xs text-gray-600"}>
          {role}
        </span>
      )
    }
  },
  {
    accessorKey: "status",
    header: "Status",
    cell: ({ row }) => (
      <div className="capitalize text-sm">{row.getValue("status")}</div>
    ),
  },
];

export default function Page() {
  // 1. Fetch data z našeho tRPC API
  const { data: users, isLoading, refetch } = trpc.getUsers.useQuery();

  return (
    <div className="container mx-auto py-10 px-4 min-h-screen flex flex-col gap-6">
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-3xl font-bold tracking-tight">Uživatelé</h1>
          <p className="text-muted-foreground">Seznam načtený přes tRPC + TanStack Table</p>
        </div>
        <Button onClick={() => refetch()} variant="outline">
          Obnovit data
        </Button>
      </div>
      
      {isLoading ? (
        <div className="p-10 border rounded-md bg-muted/20 text-center animate-pulse">
          Načítám tabulku...
        </div>
      ) : (
        // 2. Render DataTable komponenty z balíčku UI
        <div className="bg-white rounded-md shadow-sm overflow-hidden">
           <DataTable columns={columns} data={users || []} />
        </div>
      )}
    </div>
  );
}